.briefinfo {
    padding: 1rem 0;
}

.briefinfo .container {
    max-width: 1024px;
    min-width: 976px;
}

.briefinfo .container .pre {
    margin: 0.5rem 0;
}

.briefinfo .container .title {
    margin: 0.5rem 0;
    font-weight: bold;
}

.briefinfo .container .intro {
    margin: 0.5rem 0;
}

.briefinfo .container .datadisplay {
    margin: 1rem 0;
    display: flex;
    justify-content: space-between;
}

.briefinfo .container .datadisplay .databox {
    font-weight: bold;
}