@media screen and (max-width:1025px) and (min-width:425px) {
    .header {
        top: 0;
        z-index: 99;
        height: 50px;
        position: fixed;
    }

    .home {
        margin-top: 50px;
    }

    .member,
    .noticepage,
    .article,
    .products {
        margin-top: 50px;
    }

    .header .container {
        max-width: 100vw;
        min-width: 90vw;
        display: flex;
        justify-content: space-between;
    }

    .header .container .logo {
        line-height: 50px;
    }

    .header .container .logo svg {
        height: 40px;
    }

    .header .container .navs {
        display: none;
    }

    .header .container .menu-btn {
        display: block;
        display: flex;
        align-items: center;
    }

    .notice {
        padding: 0;
    }

    .notice .container {
        max-width: 100vw;
        min-width: 90vw;
        flex-direction: column-reverse;
        padding: 0;
        margin: 0;
    }

    .notice .container .protalicons {
        padding: 0;
        max-width: 100%;
    }

    .notice .container .noticepart {
        max-width: 100%;
        padding: 0.5rem 1rem;
    }

    .notice .container .protalicons .portal {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        background-color: #F7F7F8;
    }

    .notice .container .protalicons .portal .portalitem {
        max-width: 33.333333%;
    }

    .notice .container .protalicons .portal .portalitem .iconbox {
        height: 60px;
    }

    .notice .container .protalicons .portal .portalitem .iconbox img {
        background-color: #FFFFFF;
        padding: 0.5rem;
        border-radius: 5px;
    }

    .noticepage .container{
        max-width: 100vw !important;
        min-width: 90vw !important;
    }

    .briefinfo .container {
        max-width: 100vw;
        min-width: 90vw;
    }

    .group .container {
        max-width: 100vw;
        min-width: 90vw;
    }

    .product .container {
        max-width: 100vw;
        min-width: 90vw;
    }

    .activity .container {
        max-width: 100vw;
        min-width: 90vw;
    }

    .footer .content {
        max-width: 100vw;
        min-width: 90vw;
    }

    .article .articlecontent {
        max-width: 100vw !important;
        min-width: 90vw !important;
    }

    .portal .portalitem .iconbox {
        height: 60px;
    }

    .portal .portalitem .iconbox .icon {
        height: 50%;
    }

    .products .container {
        max-width: 100vw;
        min-width: 90vw;
    }

}

@media screen and (max-width:426px) {
    .header {
        top: 0;
        z-index: 99;
        height: 40px;
        position: fixed;
    }

    .home {
        margin-top: 40px;
    }

    .header .container .mobilemenu {
        margin-top: 40px;
    }

    .header .container {
        max-width: 100vw;
        min-width: 90vw;
        display: flex;
        justify-content: space-between;
    }

    .header .container .logo {
        line-height: 40px;
    }

    .header .container .logo svg {
        height: 30px;
    }

    .header .container .navs {
        display: none;
    }

    .header .container .menu-btn {
        display: block;
        display: flex;
        align-items: center;
    }

    .notice {
        padding: 0;
        margin-top: 40px;
    }

    .notice .container {
        max-width: 100vw;
        min-width: 90vw;
        flex-direction: column-reverse;
        padding: 0;
        margin: 0;
    }

    .notice .container .protalicons {
        padding: 0;
        max-width: 100%;
    }

    .notice .container .noticepart {
        max-width: 100%;
        padding: 0.5rem 1rem;
    }

    .notice .container .protalicons .portal {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        background-color: #F7F7F8;
    }

    .notice .container .protalicons .portal .portalitem {
        max-width: 33.333333%;
    }

    .notice .container .protalicons .portal .portalitem .iconbox {
        height: 60px;
    }

    .notice .container .protalicons .portal .portalitem .iconbox img {
        background-color: #FFFFFF;
        padding: 0.5rem;
        border-radius: 5px;
    }

    .briefinfo .container {
        max-width: 100vw;
        min-width: 90vw;
    }

    .group .container {
        max-width: 100vw;
        min-width: 90vw;
    }

    .group .container .intros {
        margin: 0.5rem auto;
    }

    .group .container .groups .groupname {
        margin: 0;
    }

    .product .container {
        max-width: 100vw;
        min-width: 90vw;
    }

    .activity .container {
        max-width: 100vw;
        min-width: 90vw;
    }

    .carousel-item a {
        display: none;
    }

    .footer .content {
        display: flex;
        flex-direction: column;
        max-width: 100vw;
        min-width: 90vw;
    }

    .footer .content .contentlist {
        max-width: 100%;
        margin-bottom: 0.5rem;
    }

    .article {
        margin-top: 40px;
    }

    .article .articlecontent {
        margin: 1rem 0 !important;
        max-width: 100vw !important;
        min-width: 90vw !important;
    }

    .article .articlecontent .portalpart {
        display: none;
    }

    .member {
        margin-top: 40px;
    }

    .member .container-fluid .container {
        padding: 0;
    }

    .member .container-fluid .container .membercard {
        min-width: 100% !important;
    }

    .products {
        margin-top: 40px;
    }

    .products .container {
        max-width: 100vw;
        min-width: 90vw;
    }

    .products .container .productcard {
        max-width: 100%;
    }

    .noticepage .container {
        max-width: 100vw !important;
        min-width: 90vw !important;
    }

    .noticepage .container .noticetabname {
        margin: 0;
        padding: 0.5rem !important;
    }

    .noticepage .container .noticelist .noticetext .noticeintro {
        display: none;
    }

    .noticepage .container .portalpart {
        display: none;
    }
}