.group {
    padding: 1rem 0;
}

.group .container {
    max-width: 1024px;
    min-width: 976px;
}

.group .container .picture {
    width: 100%;
}

.group .container .picture .grouppic{
    width: 100%;
}

.group .container .groups {
    display: flex;
    justify-content: center;
    margin: 0.5rem 0;
}

.group .container .groups .groupname {
    padding: 0.5rem;
    margin: 0 1rem;
    cursor: pointer;
}

.group .container .intros {
    margin: 1.5rem auto;
    max-width: 80%;
}

.group .container .intros .groupintro {
    text-align: center;
}