.joinus {
    max-height: 200px;
    height: 180px;
}

.joinus .content {
    width: 100%;
    height: 100%;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.joinus .content .title {
    text-align: center;
}

.joinus .content .subtitle {
    text-align: center;
}

.joinus .content .joinusBtn {
    opacity: 100%;
    max-width: max-content;
    padding: 0.5rem;
    border-radius: 5px;
    margin: 0 auto;
}

.joinus .content .joinusBtn a {
    color: inherit;
    text-decoration: none;
}