.portal {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    position: relative;
}

.portal .portalitem {
    min-width: 33.33333%;
    margin-bottom: 1rem;
    padding: 0;
}

.portal .portalitem .iconbox {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90px;
    width: auto;
}

.portal .portalitem .iconbox .icon {
    height: 70%;
    width: auto;
}

.portal .portalitem .portallabel {
    display: flex;
    justify-content: center;
    text-decoration: none;
}