.notice {
    padding: 1rem 0;
}

.notice .container {
    max-width: 1024px;
    min-width: 976px;
    display: flex;
    margin-top: 1rem;
    padding-left: 0;
}

.notice .container .col {
    padding: 0;
}

.noticemenu {
    display: flex;
    justify-content: space-between;
}

.noticemenu .title {
    font-weight: bold;
}

.noticemenu .noticetabs .noticetabname {
    padding: 0.1rem 0.2rem;
    margin: 0 0.2rem;
    cursor: pointer;
}

.noticepane {
    margin-top: 1rem;
}

.noticepane .noticebtn {
    padding: 0.5rem 1rem;
    border-radius: 5px;
}

.noticepane .noticelist {
    display: flex;
    flex-direction: column;
}

.noticepane .noticelist .listitem {
    margin: 0.2rem 0;
    text-decoration: none;
}

.noticepane .noticelist .listitem:hover {
    opacity: 0.78;
}