.member .container-fluid {
    margin: 2rem 0;
}

.member .container-fluid .container .picture .grouppic {
    width: 100%;
}

.member .container-fluid .container .groups {
    display: flex;
    justify-content: center;
}

.member .container-fluid .container .groups .groupname {
    padding: 0.5rem 1vw;
    margin: 0.5rem 2vw;
    cursor: pointer;
}

.member .container-fluid .container .intros {
    margin: 1.5rem auto;
    max-width: 80%;
}

.member .container-fluid .container .intros .groupintro {
    text-align: center;
}

.member .container-fluid .container .members {
    display: flex;
    flex-wrap: wrap;
}

.member .container-fluid .container .members .membercard {
    display: flex;
    flex-direction: row;
    min-width: 50%;
    margin: 0.5rem 0;
}

.member .container-fluid .container .members .membercard p {
    margin: 0;
    text-align: start;
}

.member .container-fluid .container .members .membercard .memberavatar {
    display: flex;
    justify-content: center;
    align-items: center;
}