.header {
    height: 60px;
    width: 100%;
    -moz-box-shadow: 0px 0px 10px 2px #DDDDDD;
    -webkit-box-shadow: 0px 0px 10px 2px #DDDDDD;
    box-shadow: 0px 0px 10px 2px #DDDDDD;
    position: relative;
}

.header .container {
    max-width: 1024px;
    min-width: 976px;
    display: flex;
}

.header .container .logo {
    line-height: 60px;
}

.header .container .logo svg {
    height: 40px;
}

.header .container .mobilemenu {
    z-index: 99;
    position: fixed;
    margin-top: 50px;
    width: 100%;
    height: 100%;
    left: 0;
    right: 0;
}

.header .container .mobilemenu .nav-item {
    text-align: center;
}

.header .container .nav {
    margin-left: 10vw;
}

.header .container .nav .nav-item .level1 {
    padding: 0 1rem;
    line-height: 58px;
}


.header .container .nav .level1 .dropdown-toggle {
    padding: 0 1rem;
    color: inherit;
    line-height: 60px;
}

.header .container .nav .level1 {
    background: inherit;
}

.header .container .nav .level1 .dropdown-menu {
    background: inherit;
}

.header .container .nav .nav-item .active {
    border-bottom: 2px solid;
    color: inherit;
}

.header .container .menu-btn {
    display: none;
}

.navline {
    padding: 0;
    width: 30px;
    height: 2px;
    margin: 6px 0;
    transition: all 300ms cubic-bezier(.8, 0, .33, 1);
}

.navline-cross-1 {
    padding: 0;
    width: 30px;
    height: 2px;
    margin: 5px 0;
    transform: rotate(45deg) translateY(10px);
    transition: all 300ms cubic-bezier(.8, 0, .33, 1);
}

.navline-cross-2 {
    padding: 0;
    width: 30px;
    height: 2px;
    margin: 5px 0;
    transform: translate(-20px);
    opacity: 0;
    transition: all 300ms cubic-bezier(.8, 0, .33, 1);
}

.navline-cross-3 {
    padding: 0;
    width: 30px;
    height: 2px;
    margin: 5px 0;
    transform: rotate(-45deg) translateY(-10px);
    transition: all 300ms cubic-bezier(.8, 0, .33, 1);
}