.footer {
    width: 100%;
    padding-top: 2rem;
}

.footer .content {
    margin: 0 auto;
    max-width: 1024px;
    min-width: 976px;
    display: flex;
    padding: 0 auto;
    margin-bottom: 1rem;
}

.footer .content .footer-title {
    margin-bottom: 1rem 0;
    color: inherit;
}

.footer .content .footer-item {
    display: block;
    margin: 0.5rem 0;
    text-decoration: none;
    color: inherit;
}

.footer .content .qrcode {
    max-width: 120px;
    margin-top: 0.5rem;
}

.footer .copyright {
    width: 100%;
    padding: 0 0 1rem 0;
    text-align: center;
}