.product {
    padding: 1rem 0;
}

.product .container {
    max-width: 1024px;
    min-width: 976px;
}

.product .container .pre {
    margin: 0.5rem 0;
}

.product .container .title {
    margin: 0.5rem 0;
    font-weight: bold;
}

.product .container .intro {
    margin: 0.5rem 0;
}

.product .container .more {
    text-decoration: none;
}

.product .container .productdisplay {
    display: flex;
    justify-content: space-between;
    margin: 1rem 0;
}

.product .container .productdisplay .productname {
    margin: 0.5rem 0;
    font-weight: bold;
    text-decoration: none;
}

.product .container .productdisplay .productintro {
    margin: 0.5rem 0;
}

.products .container {
    max-width: 1024px;
    min-width: 976px;
}

.products .container .productcard {
    display: flex;
    margin: 0.5rem 0;
}

.products .container .productcard .productimg {
    display: flex;
    justify-content: center;
    align-items: center;
}

.products .container .productcard .productimg img{
    width: 100%;
}

.products .container .productcard .producttext p {
    margin: 0;
}

.products .container .productcard .producttext a {
    text-decoration: none;
}

.products .container .productlist {
    display: flex;
    flex-wrap: wrap;
}