.noticepage .container {
    max-width: 1024px;
    min-width: 976px;
}

.noticepage .container .noticetabname {
    padding: 0.5rem 1rem;
    cursor: pointer;
    margin-left: 1vw;
}

.noticepage .container .noticelist {}

.noticepage .container .noticelist .noticeitem {
    margin: 1rem 0;
}

.noticepage .container .noticelist .noticeimg {
    display: flex;
    justify-content: center;
    align-items: center;
}

.noticepage .container .noticelist .noticetext p {
    margin: 0.2rem;
}

.noticepage .container .noticelist .noticetext a {
    text-decoration: none;
}